.prof_Section{
    border-radius: 20px;
    border: black solid;
    background: rgba(55,55,55,0.7);
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto 1fr 1fr;
    display:grid;
    grid-template-areas:
    'sett sett'
    'ava desc'
    'prod prod';
}
.prof_Sett{
    grid-area: sett;
}
.prof_Ava{
    grid-area: ava;
    display: block;
}
.prof-cont{
    color: rgb(155,79,17);
    background: rgba(55,55,55,0.4);
    width: fit-content;
    margin: auto;
}
.prof_Desc{
    grid-area: desc;
}
.prof-de{
    color: rgb(155,79,17);
    width: fit-content;
    margin: auto;
}
.prof_Prod{
    grid-area: prod;
    display: grid;
    border: #3D2118 solid;
}
.prof-pro{
 margin: auto;
    color: rgb(155,79,17);
}
/*####################################################################*/
.prof_Section_setting_img{
    height: 40px;
    width: 40px;
    border-radius: 20px;
}
.Panel-Ava{
    height: 200px;
    width: 200px;
    border-radius: 20px;
}


.img-but{
    width: 30px;
    height: 30px;
    margin: 0;
    position: relative;
    top: 3px;
}


